.Auth{
    display: flex;
    gap: 4rem;
    position: relative;
    height: 100vh;
    justify-content: center;
    align-items: center;
}
.Auth>div{
    display: flex;
    align-items: center;
    justify-content: center;
}
.a-left{
    gap: 2rem;
}
.a-left>img{
    width: 4rem;
    height: 4rem;
}
.Webname>h1{
    font-size:3rem;
    background-color: red;
    background-image:var(--buttonBg);
    background-size: 100%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}
.Webname>h6{
    font-size:0.75rem;
}
.infoform{
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    gap: 2rem;
}
.infoInput{
    border: none;
    outline: none;
    background-color:var(--inputColor);
    border-radius:8px;
    padding: 20px;
    flex: 1;
}
.infoform>div{
    display: flex;
    gap: 1rem;
    height: 2rem;
    width: 90%;
}
.infoButton{
    width: 6rem;
    height: 2rem;
    align-self: flex-end;
}
.authForm{
    background-color: var(--cardColor);
    border-radius: 1rem;
    padding: 1rem;
}
.authForm>div{
    width: 100%;
    align-items: center;
    justify-content: center;
}